<!--  -->
<template>
  <div>
      <Zi bool1="true"  goback="true" con="搜索地址"  @click="this.bian" >
        <h1 @click="$router.go(-1)" slot="left">
        <
      </h1>
      </Zi>
       <div  class="diw">
       <input v-model="val" type="text">
      <van-button @click="search" type="info">确认</van-button>
        </div>
        <p class="man">为了满足商家的送餐要求，建议您从列表中选择地址</p>
        <ul class="shang" v-show="bool==false||list.length!==0">
            <li @click="add(item)" :key="index" v-for="(item,index) of list">
                <p>{{item.name}}</p>
                <p>{{item.address}}</p>
            </li>
        </ul>
        <div class="man2" v-show="bool==true||list.length==0">
            <p>找不到地址？</p>
            <p>请尝试输入小区、写字楼或学校名</p>
            <p>详细地址（如门牌号）可稍后输入哦。</p>
        </div>
  </div>
</template>

<script>
import Zi from "../../../components/header"
export default {
  data () {
    return {
        val:"",
        list:[],
        bool:true
    };
  },

  components: {Zi},

  computed: {},

  mounted(){},

  created(){},

  methods: {
      search(){
        
          var that=this
          this.http.get("https://elm.cangdu.org/v1/pois?type=nearby&keyword="+this.val).then((d)=>{
           if(d.data.length>0){
              that.list=d.data
              that.bool=false
           }
          })
      },
      add(item){
          this.$router.push({path:"/address/addaddress",query:{name:item.name}})
      }
  }
}

</script>
<style scoped lang="scss">
.diw{
    line-height: 1rem;
    height: 1rem;
    width: 90%;
    margin-top: 1rem;
    background: white;
    padding:0 5%;
    input{
        width: 74%;
        height: 0.6rem;
        background: #ddd;
        border-radius: 0.1rem;
     
    }
    button{
        margin-left: 0.1rem;
          width: 20%;
        height: 0.64rem;
        line-height: 0.64rem;
        font-size: 0.2rem;

    }
}
.man{
        background: #fff6e4;
    font-size: .22rem;
    color: #ff883f;
    text-align: center;
    padding: .2rem 0;
}
.man2{
margin-top: 0.4rem;
    p{
        width: 100%;
    text-align: center;
    font-size: .24rem;
    color: #969696;
    margin-bottom: .2rem;
    }
}
.shang{
        li{
            height: 1rem;
            p{
                margin-left: 0.3rem;
                color: #969696;
                line-height: 0.5rem;
               
            }
             border-bottom: 1px solid #ccc;
        }
}
</style>


